
import * as db from "~/core/database";

export const insert = ({ name, siteCode, city, state }) =>
  db.insert(
    `
      insert into sites(
        name
        , siteCode
        , city
        , state
        , createdAt
        , modifiedAt
      ) values (
        ?
        , ?
        , ?
        , ?
        , now()
        , now()
      )
    `,
    [name, siteCode, city, state]
  );
